@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.home {
  border-radius: 10px;
  width: 65vw;
  height: 80vh;
  background: #fff;
  overflow: hidden;
  display: flex;
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}
@media screen and (max-width: 500px) {
  .home {
    width: 100vw;
    height: 100vh;
    border-radius: 0;
  }
}
.sidebar {
  flex: 1 1;
  background-color: #3e3c61;
  position: relative;
}
@media screen and (max-width: 500px) {
  .sidebar {
    display: none;
  }
}
.sidebar__header {
  display: flex;
  align-items: center;
  background-color: #2f2d52;
  height: 70px;
  padding: 10px;
  justify-content: space-between;
  color: #ddddf7;
}
.sidebar__header .logo {
  font-weight: bold;
  display: flex;
  grid-gap: 2%;
  gap: 2%;
}
.sidebar__header .user {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  align-items: center;
}
.sidebar__header .user button {
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  background-color: #5d5b8d;
  color: #ddddf7;
  font-size: 1.3rem;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.sidebar__header .user button:hover {
  background-color: #8da4f1;
  color: #2f2d52;
}
.sidebar__header .user button:active {
  scale: 0.9;
}
.sidebar__main-room {
  height: 100px;
  display: flex;
  grid-gap: 10%;
  gap: 10%;
  align-items: center;
  justify-content: center;
  background-color: #49476e;
}
.sidebar__main-room .icon {
  width: 60px;
  height: 60px;
  border: 2px solid #7b96ec;
  border-radius: 50%;
}
.sidebar__main-room .icon img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.sidebar__main-room span {
  text-align: center;
  color: #ddddf7;
  width: 40%;
}
.sidebar__main-room span em {
  font-weight: bold;
}
.sidebar__main-users {
  overflow: auto;
}
.sidebar__main-users .user {
  height: 50px;
  padding: 10px 40px;
  color: white;
  display: flex;
  align-items: center;
  grid-gap: 10%;
  gap: 10%;
}
.sidebar__main-users .user:hover {
  background-color: #2f2d52;
}
.chat {
  flex: 2 1;
}
.chat__header {
  height: 70px;
  background-color: #5d5b8d;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  color: lightgray;
}
.chat__header div {
  display: flex;
  align-items: center;
  grid-gap: 10%;
  gap: 10%;
  color: white;
}
.messages {
  padding: 2%;
  overflow: auto;
  display: flex;
  flex-direction: column;
  background-color: #ddddf7;
  height: calc(100% - 140px);
  overflow: auto;
}
.messages .message {
  width: 250px;
  margin-left: auto;
  margin-right: 0;
  -webkit-animation: grow 0.3s cubic-bezier(0.01, 0.75, 0.96, 0.6);
          animation: grow 0.3s cubic-bezier(0.01, 0.75, 0.96, 0.6);
}
.messages .message__author {
  text-align: end;
  color: gray;
  margin-bottom: 1rem;
  margin-right: 15%;
}
.messages .message__box {
  position: relative;
  padding: 5%;
  background-color: white;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
}
.messages .message__box::before {
  content: "";
  position: absolute;
  top: -1rem;
  right: 2rem;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 0 1rem 1rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  border-color: transparent transparent white transparent;
}
.messages .message__box--time {
  margin-top: 0.3rem;
  align-self: flex-end;
  font-style: italic;
  color: gray;
}
.messages .message__box--text {
  font-size: 1.2rem;
  word-wrap: break-word;
}
.messages .message.isMine {
  margin-left: 0;
  margin-right: auto;
}
.messages .message.isMine .message__author {
  text-align: start;
  margin-left: 15%;
}
.messages .message.isMine .message__box {
  background-color: #8da4f1;
  color: white;
}
.messages .message.isMine .message__box::before {
  right: auto;
  left: 2rem;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  border-color: transparent transparent #8da4f1 transparent;
}
.messages .message.isMine .message__box--time {
  color: white;
}

@-webkit-keyframes grow {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes grow {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.form {
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
}
.form form {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.form form input[type=text] {
  font-size: 1rem;
  padding: 10px;
  height: 40px;
  width: 80%;
  border: none;
  outline: none;
  font-size: 1.1rem;
}
.form form input[type=text]::-webkit-input-placeholder {
  color: lightgray;
}
.form form input[type=text]::placeholder {
  color: lightgray;
}
.form form .btn {
  width: 20%;
  height: 100%;
  background-color: #7b96ec;
  color: white;
}
.form form .btn:active {
  background-color: #2f2d52;
}
.join {
  width: 500px;
  color: #fff;
  border-radius: 10px;
  overflow: hidden;
}
.join__header {
  text-align: center;
  padding: 20px;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 5%;
  gap: 5%;
  color: #5d5b8d;
}
.join__header .icon {
  border-radius: 50%;
  border: 2px solid #fff;
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.join__main {
  padding: 30px 40px;
  background: white;
}
.join__main .error {
  color: tomato;
  text-align: center;
  font-size: 1.2rem;
  padding-bottom: 1rem;
}
.join__main form {
  display: flex;
  flex-direction: column;
}
.join__main form .form-control {
  margin-bottom: 20px;
}
.join__main form .form-control input[type=text] {
  font-size: 16px;
  padding: 10px;
  height: 40px;
  width: 100%;
  border: none;
  outline: none;
  border-bottom: 2px solid #8da4f1;
}
.join__main form .languages {
  height: 200px;
  display: flex;
  flex-wrap: wrap;
  padding: 1rem 2rem;
  grid-gap: 27%;
  gap: 27%;
  overflow: hidden;
}
@media screen and (max-width: 500px) {
  .join__main form .languages {
    grid-gap: 24%;
    gap: 24%;
    padding: 1rem 0;
  }
}
.join__main form .languages .flag {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid rgb(175, 175, 175);
  cursor: pointer;
  transition: all ease-in-out 0.1s;
}
.join__main form .languages .flag.active {
  scale: 1.2;
  border: 3px solid #8da4f1;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}
.join__main form .languages .flag img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.join__main form .btn {
  margin-top: 20px;
  width: 40%;
  padding: 10px;
  align-self: center;
  transition: all ease-in-out 0.1s;
}
@media screen and (max-width: 500px) {
  .join__main form .btn {
    width: 60%;
  }
}
.join__main form .btn:active {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

@media screen and (max-width: 500px) {
  .join {
    width: 100vw;
    height: 100vh;
    border-radius: 0px;
  }
  .join__header {
    height: 30%;
  }
  .join__main {
    height: 70%;
  }
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  background: #a7bcff;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.btn {
  cursor: pointer;
  padding: 5px;
  background: #7b96ec;
  color: #ddddf7;
  border: 0;
  font-size: 1rem;
}
.btn:hover {
  background-color: #8683b3;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #2f2d52;
  border-radius: 10px;
}
